<template>
    <!-- <Mycircle :diam=300 :n=100></Mycircle>
    <Mycircle :diam=250 :n=30></Mycircle>
    <Mycircle :diam=200 :n=7></Mycircle> -->

    <div id="maker" ref="maker">
        <Mycircle v-for="(item,i) in Circle_Maker" :key="i"
            :diam=diam-(i*50) :Items=item.items :On_i=item.On_i :Degree=item.Degree
        ></Mycircle>

        <!-- <Mycircle v-for="(item,i) in Circle_Maker" :key="i"
            :diam=diam-((5-i)*50) :Items=item.items :On_i=item.On_i :Degree=item.Degree
        ></Mycircle> -->

        <!-- <Mycircle :style="`top:0px; left:20px;`" :diam=150 :Items=days.items :On_i=days.On_i :Degree=days.Degree></Mycircle> -->
    </div>

    <h4>{{thiswidth}}</h4>

</template>
<script>
import Mycircle from './Mycircle.vue'
export default {
    mounted() {  
        this.setThisday();
            
        this.setDiam();
    },  
    
    data(){
        return{
            thiswidth:  'abcd',//this.getWidth(),
            diam:100,//this.getWidth(),
            // diam:600,
            // diam:500,
            // diam : (this.offsetWidth*20),
            // diam : this.offsetWidth,

            years : {items: [],On_i : Number,Degree :Number},
            // months : {items: [],On_i: Number,Degree :Number},
            months : {items: ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec",],On_i: Number,Degree :Number} ,
            date : {items: [],On_i: Number,Degree :Number},            
            days : {items: ["Mon","Tue","Wed","Thur","Fri","Sat","Sun"],On_i: Number,Degree :Number} ,
            hours : {items: [],On_i: Number,Degree :Number},          
            
            Circle_Maker : [],
            // day :[],
            // months :['1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월'],
            // days : ["월","화","수","목","금","토","일"],
        }
    },
    methods : {
        setDiam()
        {
            // window.screen.width
            if(window.screen.width<window.screen.height)
                this.diam = window.screen.width-50;
            else
                this.diam = window.screen.height-50;
        },
        getWidth() {
            console.log(this.$refs.maker.clientWidth);
            // return result;
        },

        // getWidth()
        // {
            
        //     // let box = document.querySelector('#maker');
        //     // let width = box.clientWidth;
        //     return ''+this.$refs.maker.clientWidth;
        // },
        getYears(birth){
            // for(var i=birth;i<birth+100;i+=3)
            var currentTime = new Date();
            var cnt = 0;
            for(var i=birth;i<birth+81;i+=2 )//92년생 남 기대수명. 
            {
                this.years.items.push(i);
                // this.years.items.push((i%100)+'년');
                if(i==currentTime.getFullYear())
                   this.years.On_i = cnt;

            cnt ++;
            }
            },
        setThisday(birth = 1992)
            {
                this.getYears(birth);                
                for(var i=1;i<=31;i++)this.date.items.push(i);//+'일');
                // for(i=1;i<=12;i++)this.months.items.push(i+'월');
                
                for(i=0;i<=12;i++)this.hours.items.push(i+'am');
                for(i=1;i<=11;i++)this.hours.items.push(i+'pm');


                var currentTime = new Date();
                this.months.On_i= currentTime.getMonth();
                this.date.On_i= currentTime.getDate()-1;
                this.days.On_i= currentTime.getDay()-1;
                this.hours.On_i= currentTime.getHours()-1;

                this.years.Degree= (currentTime.getFullYear()-birth)/81*360;//평균수명.
                this.months.Degree= (currentTime.getMonth())/12*360;
                this.date.Degree= (currentTime.getDate())/30*360;
                this.days.Degree= (currentTime.getDay()/7)*360;
                this.hours.Degree= (currentTime.getHours())/24*360;

                this.Circle_Maker.push(this.years);
                this.Circle_Maker.push(this.months);
                this.Circle_Maker.push(this.date);
                this.Circle_Maker.push(this.days);
                this.Circle_Maker.push(this.hours);
            }
  },
  components: { Mycircle },
    
}
</script>
<style>

#maker{
    position: fixed;
    width: 100%;
    height: 100%;
    /* background :red; */
    background: #262626;
}
    
   

</style>